export class User {
    type?: string;
    id!: number;
    view_href!: string;
    login!: string;
    online_status!: string;
    avatar!: {
        profile: string;
    };
    posts!: number;
}

export class Li_Response {
    data!: {
        items: [],
        list_item_type: string,
        next_cursor: string,
        size: number,
        type: string,
        count: number
    };
    http_code!: number;
    message?: string;
    status!: string;
}

export class Message {
    type!: string;
    id!: string;
    view_href!: string;
    author!: {
        login: string, 
        id: string,
        view_href: string
    };
    subject!: string;
    body!: string;
    teaser?: string;
    board!: {
        id: string,
        view_href: string
    };
    conversation!: {
        id: string,
        view_href: string,
        messages_count: number,
        solved: boolean
        last_post_time: string,
        last_post_time_friendly: string
    };
    post_time!: string;
    post_time_friendly!: string;
    is_solution!: boolean;
    replies!: {
        query: string
    };
}

export class Tag {
    response!: {
        tags: {
            tag:  [{
                attributes: {
                    href: string,
                    mixins: string,
                    type: string
                },
                id: string,
                mixin: {
                    cloud: {
                        weight: string
                    }
                },
                text: string
            }]
        },
        value: string
    };
}

export class Tag_Child {
    attributes?: {
        href: string,
        mixins: string,
        type: string
    };
    id!: string;
    mixin!: {
        cloud: {
            weight: string
        }
    };
    text!: string;
}