import { Component, OnInit } from '@angular/core';
import { Li_Response, Message, User } from '../models/model-interfaces';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { MatTableDataSource } from '@angular/material/table';
import {animate, state, style, transition, trigger} from '@angular/animations';


const base_url: string = "https://community.khoros.com/api/2.0/";
//const base_url: string = "https://developer.community.boschrexroth.com/api/2.0/";
//const base_url: string = "https://community.ebay.de/api/2.0/";

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})

export class UserComponent implements OnInit {
  users: User[] = [];
  all_users!: User[] ;
  selected_user_array?: User[];
  selected_user?: User;
  posts_per_user: Message[] = [];
  dataSource!: MatTableDataSource<Message>;
  response!: Li_Response;
  length = 100;
  pageSize = 20;
  index = 0;
  pageSizeOptions: number[] = [10, 20, 50, 100];
  inputValue?: string;
  messages_per_user!: Message[];
  displayedColumns: string[] = ["subject", "board", "posted", "replies", "solved", "link"];
  not_found: boolean = false;
  expandedElement!: Message | null;
  latest_answer!: Message[];
  input_search_user!: string;
  
  
  constructor(private http: HttpClient) { 
    
  }

  ngOnInit(): void {
    this.http.get<Li_Response>(base_url + "search?q=SELECT%20login,%20id,%20view_href,%20online_status,%20avatar%20FROM%20users%20LIMIT%20100").subscribe(res => {
      this.response = res;
      this.all_users = this.response.data.items;  
      this.all_users.sort(function (a, b) {
        return ('' + a.login).localeCompare(b.login);
      });
      this.getPostsSizePerUser();
      this.requestUser(this.pageSize, this.index);
    },
    (error: HttpErrorResponse) => {
      switch (error.status) {
        case 403:
          console.log("Kein Zugriff: " + error);
      }
    }
    );
  }

  async getPostsSizePerUser(){
    for (var i=0; i<this.all_users.length; i++){
      const size_response = await this.http.get<Li_Response>("https://community.khoros.com/api/2.0/search?q=SELECT%20count(*)%20FROM%20messages%20WHERE%20author.id='" + this.all_users[i].id + "'").toPromise();
      this.all_users[i].posts = size_response.data.count;
    }
    
  }

  requestUser (size: number, index:number){
    this.users = [];
    var n = 0;
    for (var i=index*size; i<(index+1)*size; i++){
      this.users[n] = this.all_users[i];
      n++;
    }
  }

  async searchPostsPerUser() {
    this.http.get<Li_Response>(base_url + "search?q=SELECT%20login,%20id%20FROM%20users%20WHERE%20login='" + this.inputValue + "'").subscribe(res => {
      this.response = res;
        if (this.response.http_code === 200){
          this.selected_user_array = this.response.data.items;
          if (this.selected_user_array.length > 0){
            this.selected_user = this.selected_user_array[0];
            this.not_found = false;
          } else {
            this.not_found = true;
          }
        } else {
          this.selected_user_array = [];
          this.selected_user = undefined;
        }
    });
    const response_posts = await this.http.get<Li_Response>(base_url + "search?q=SELECT%20id,%20type,%20author,%20subject,%20body,%20teaser,%20board,%20conversation,%20post_time,%20post_time_friendly,%20replies%20FROM%20messages%20WHERE%20conversation.style='forum'%20AND%20depth=0%20AND%20author.login='" + this.inputValue + "'").toPromise(); 
    if (response_posts.http_code === 200){
      this.messages_per_user = response_posts.data.items;
    } else {
      this.messages_per_user = [];
      console.error("Messages cannot be set!");
    }
    for (var i=0; i<this.messages_per_user.length; i++){
      this.messages_per_user[i].replies.query = "<p>Waiting ...</p>"
    }
    this.dataSource = new MatTableDataSource<Message>(this.messages_per_user);
    for (var i=0; i<this.messages_per_user.length; i++){
      const response_replies = await this.http.get<Li_Response>(base_url + "search?q=SELECT%20*%20FROM%20messages%20WHERE%20parent.id='" + this.messages_per_user[i].id + "'%20ORDER%20BY%20post_time%20DESC").toPromise();
      this.latest_answer = response_replies.data.items;
      if (this.latest_answer.length > 0){
        if (this.messages_per_user[i].conversation.solved === true){
          for (var n=0; n<this.latest_answer.length; n++){
            if (this.latest_answer[n].is_solution === true){
              this.messages_per_user[i].replies.query = this.latest_answer[n].body;
              break;
            }
          }
        } else {
          this.messages_per_user[i].replies.query = this.latest_answer[0].body;
        }
      } else {
        this.messages_per_user[i].replies.query = "<p>There is no answer!</p>";
      }
      var img = document.getElementsByTagName("img");
      for (var j=0; j<img.length; j++){
        img[j].setAttribute("style", "width: 100%");
      }
    }
    this.dataSource = new MatTableDataSource<Message>(this.messages_per_user);
  }

  keyDown (event:any){
    if (event.keyCode === 13){
      this.searchPostsPerUser();
    }
  }

  clearUser () {
    this.inputValue='';
    this.selected_user = undefined;
    this.not_found = false;
  }

  onSubmit() {
    console.log("Submit successful!");
    console.log(this.input_search_user);
  }
}