import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ChartOptions, ChartType } from 'chart.js';
import { Label, SingleOrMultiDataSet } from 'ng2-charts';
import { Li_Response, Tag } from '../models/model-interfaces';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { plainToClass } from 'class-transformer';

const base_url: string = "https://community.khoros.com/api/2.0/";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  response!: Li_Response;
  doughnutChartLabels: Label[] = ["Online", "Offline"];
  doughnutChartData: SingleOrMultiDataSet = [0, 0];
  doughnutChartType: ChartType = "doughnut";
  doughnutChartColor: any[] = [{ backgroundColor: ["#19ff66", "#bcc2be"]}];
  barChartColor: any[] = [ { backgroundColor: ["#ddcfa6", "#a4c4fc", "#eaaca6"]}];
  barChartLabels: Label[] = ["Users", "Posts", "Tags"];
  barChartData: SingleOrMultiDataSet = [0, 0, 0];
  barChartType: ChartType = "bar";
  barChartOptions: ChartOptions = {
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true
        }
      }]
    }
  };
  online_user!: number;
  offline_user!: number;
  all_users: number = 0;
  tags: string[] = [];
  tag!: Tag;
  tag_count!: Tag;

  constructor(private http: HttpClient, private xmlToJsonService: NgxXml2jsonService) { }

  ngOnInit(): void {
    this.http.get<Li_Response>(base_url + "search?q=SELECT%20count(*)%20FROM%20users%20WHERE%20online_status='online'").subscribe(res => {
      this.response = res;
      if (this.response.http_code === 200) {
        this.online_user = this.response.data.count;
        this.http.get<Li_Response>(base_url + "search?q=SELECT%20count(*)%20FROM%20users").subscribe(res => {
          this.response = res;
          if (this.response.http_code === 200) {
            this.all_users = this.response.data.count;
            this.offline_user = this.all_users - this.online_user;
            this.doughnutChartData = [this.online_user, this.offline_user];
          }
        });
      } else {
        alert("Online-Status der User kann nicht abgefragt werden!");
      }      
    });
    this.getTags();
    this.getBarChartData();
}

getTags(){
  this.http.get("https://community.khoros.com/restapi/vc/tagging/tags/all?page_size=1000", { responseType: "text"}).subscribe(res => {
    const parser = new DOMParser();
    var xml = parser.parseFromString(res, 'text/xml');
    var json = this.xmlToJsonService.xmlToJson(xml);
    this.tag = plainToClass(Tag, json);
    this.tag.response.tags.tag.sort(function(a,b){
      var c: number = +a.mixin.cloud.weight;
      var d: number = +b.mixin.cloud.weight;
      return d-c;
    });
    for (var i=0; i<30; i++){
      console.log(this.tag.response.tags.tag[i].mixin.cloud.weight);
      this.tags.push(this.tag.response.tags.tag[i].text);
    }
  });
}

async getBarChartData(){
  var user = 0;
  var posts = 0;
  var tags = 0;

  const posts_response = await this.http.get<Li_Response>(base_url + "search?q=SELECT%20count(*)%20FROM%20messages").toPromise();
  posts_response.http_code === 200 ? posts = posts_response.data.count : posts = 0;

  const user_response = await this.http.get<Li_Response>(base_url + "search?q=SELECT%20count(*)%20FROM%20users").toPromise();
  user_response.http_code === 200 ? user = user_response.data.count : user = 0;
  
  const tag_response = await this.http.get("https://community.khoros.com/restapi/vc/tagging/tags/all/count", {responseType: "text"}).toPromise();
  console.log(tag_response);
  const parser = new DOMParser();
  var xml = parser.parseFromString(tag_response, 'text/xml');
  var json = this.xmlToJsonService.xmlToJson(xml);
  this.tag_count = plainToClass(Tag, json);
  tags =+ this.tag_count.response.value;

  this.barChartData = [user, posts, tags];
}
}
