import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { plainToClass } from 'class-transformer';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { Observable } from 'rxjs';
import { Li_Response, Message, Tag, Tag_Child } from '../models/model-interfaces';
import { map } from 'rxjs/operators';

const parser = new DOMParser();
const base_url = "https://community.khoros.com/api/2.0/"

@Component({
  selector: 'app-tags',
  templateUrl: './tags.component.html',
  styleUrls: ['./tags.component.css']
})
export class TagsComponent implements OnInit {
  tags: string[] = [];
  tag_object!: Tag;
  tags_selected: Tag_Child[] = [];
  tags_all: Tag_Child[] = [];
  length: number = 1000;
  pageSize: number = 100;
  index: number = 0;
  pageSizeOptions: number[] = [100, 250, 500, 1000]
  tag_entries: Message[] = [];
  dataSource!: MatTableDataSource<Message>;
  displayedColumns: string[] = ["subject", "author", "board", "posted", "link"];
  tagControl = new FormControl();
  filteredOptions!: Observable<Tag_Child[]>;

  constructor(private http: HttpClient, private xmlToJsonService: NgxXml2jsonService) { }

  ngOnInit(): void {
    this.getTags();
    this.filteredOptions = this.tagControl.valueChanges.pipe(map(value => this._filter(value)));
  }

  //Filter suggested values for tag search
  private _filter(value: string): Tag_Child[] {
    const filterValue = value.toLowerCase();
    return this.tags_all.filter(option => option.text.toLowerCase().includes(filterValue));
  }

  //Set size of the paginator and get all tags from the community 
  async getTags() {
    this.tag_object = this.convertXmlToTagObject(await this.http.get("https://community.khoros.com/restapi/vc/tagging/tags/all/count", { responseType: "text"}).toPromise());
    this.length = parseInt(this.tag_object.response.value);

    for (var i=1; i<=Math.ceil(this.length/1000); i++){
      this.tag_object = this.convertXmlToTagObject(await this.http.get("https://community.khoros.com/restapi/vc/tagging/tags/all?page_size=1000&page=" + i, { responseType: "text"}).toPromise());
      for (var n=0; n<this.tag_object.response.tags.tag.length; n++){
        this.tags_all.push(this.tag_object.response.tags.tag[n]);
      }
    }
    this.requestTags(this.pageSize, this.index);
  }

  //Convert XML responses to the corresponding object
  convertXmlToTagObject(response: any): Tag {
    var xml = parser.parseFromString(response, "text/xml");
    var json = this.xmlToJsonService.xmlToJson(xml);
    return plainToClass(Tag, json);
  }

  //Show all tags for the requested size and page-index of the paginator
  requestTags(size: number, index: number){
    this.tags_selected = [];
    for (var i=index*size; i<(index+1)*size; i++){
      if (i < this.tags_all.length){
        this.tags_selected.push(this.tags_all[i]);
      }
    }
  }

  //Add Tags to the tag-array and show the corresponding entries
  addTag(){
    this.tags.push(this.tagControl.value);
    this.tagControl.setValue("");
    this.showTags();
  }

  //Show all entries for the selected tags
  showTags(){
    var tag: string[] = [...this.tags];
    for (var i=0; i<tag.length; i++){
      while (tag[i].indexOf(" ") > -1){
        tag[i] = tag[i].replace(" ", "%20");
      }
    }
    var apiUrl: string = "search?q=SELECT%20*%20FROM%20messages%20WHERE%20tags.text=";
    for (var i=0; i<tag.length; i++){
      apiUrl += ("'" + tag[i] + "'");
      if (i !== (tag.length-1)){
        apiUrl += ("%20AND%20tags.text=");
      }
    }
    this.http.get<Li_Response>(base_url + apiUrl).subscribe(res => {
      this.tag_entries = res.data.items;
      this.dataSource = new MatTableDataSource<Message>(this.tag_entries);
    });
  }

  //remove Tags from the tag list and the tag array
  removeTag(event: any){
    event.path[3].remove();
    var text:string = event.path[3].childNodes[1].data.substring(1);
    text = text.slice(0, -1);
    for (var i=0; i<this.tags.length; i++){
      if (this.tags[i] === text) {
        var arrayIndex = this.tags.indexOf(text);
        if (arrayIndex > -1) this.tags.splice(arrayIndex, 1);
      }
    }
    if (this.tags.length > 0){
      this.showTags();
    } else {
      this.tag_entries = [];
      this.dataSource = new MatTableDataSource(this.tag_entries);
    }
  }

  //Execute addTag()-Method on Enter
  keyDown (event:any){
    if (event.keyCode === 13){
      this.addTag();
    }
  }



}
