import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Li_Response, Message } from '../models/model-interfaces';

const base_url = "https://community.khoros.com/api/2.0/";

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})

export class PostsComponent implements OnInit {
  messages_selected: Message[] = [];
  all_messages: Message[] = [];
  message_size: number = 0;
  messages_ready: boolean = false;

  inputValue: string = "";
  not_found: boolean = false;

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.getMessages();
  }

  async getMessages(){
    this.messages_ready = false;
    const message_response = await this.http.get<Li_Response>(base_url + "search?q=SELECT%20*%20FROM%20messages%20WHERE%20depth=0%20LIMIT%20100").toPromise();
    this.all_messages = message_response.data.items;
    switch(this.message_size) {
      case 0: {
        this.message_size = 10;
        for (var i=0; i<this.message_size; i++){
          this.messages_selected[i] = this.all_messages[i];
        }
        this.messages_ready = true;
        break;
      }
      case 10: {
        this.message_size = 25;
        for (var i=10; i<this.message_size; i++){
          this.messages_selected[i] = this.all_messages[i];
        }
        this.messages_ready = true;
        break;
      }
      case 25: {
        this.message_size = 50;
        for (var i=25; i<this.message_size; i++){
          this.messages_selected[i] = this.all_messages[i];
        }
        this.messages_ready = true;
        break;
      }
      case 50: {
        this.message_size = 100;
        for (var i=50; i<this.message_size; i++){
          this.messages_selected[i] = this.all_messages[i];
        }
        break;
      }
    }
  }
  
clearTitle(){
  this.inputValue = "";
  this.not_found = false;
}

searchPost(){
  if (this.inputValue == "") alert("Please enter a post title!");
}

}
